.message-email{
    background-color: #9f5b26;
    width: 75px;
    height: 75px;
    border-radius: 100px;
    position: fixed;
    bottom: 25px;
    right: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    color: white;
    z-index: 100;
    cursor: pointer;
}

@media (max-width: 768px) {
    .message-email{
        width: 125px;
        height: 125px;
        border-radius: 100px;
        font-size: 60px;

    }
}

@media (min-width: 1920px) {
    .message-email{
        width: 150px;
        height: 150px;
        border-radius: 100px;
        font-size: 70px;

    }
}

.form-email{
    position: fixed;
    bottom: 25px;
    right: 25px;
    width: 300px;
    z-index: 100;
}

.form-email .email-header{
    background-color: #9f5b26;
    width: 100%;
    font-family: gotham;
    border-radius: 10px 10px 0px 0px;
    display: flex;
    padding: 10px;
    justify-content: space-between;
    align-items: center;
}

.form-email .email-header p{
    font-size: 20px;
}

.form-email .email-header a{
    font-size: 20px;
    text-decoration: none;
    color: black;
    cursor: pointer;
}

.form-email .email-header a{
    font-size: 20px;
    text-decoration: none;
    color: black;
}

.form-email .email-body{
    background-color: #dfa533;
    width: 100%;
    font-family: gotham;
    display: flex;
    flex-direction: column;
    padding: 10px;
    justify-content: center;
    align-items: center;
}

.form-email .email-body .text-field{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 5px;
}

.form-email .email-body .text-field p{
    margin-bottom: 5px;
}

.form-email .email-body .text-field input{
    width: 100%;
    height: 20px;
    
}

.form-email .email-body .text-field input:focus{
    outline: none;
}

.form-email .email-body .message-field{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 5px;
}

.form-email .email-body .message-field p{
    margin-bottom: 5px;
}

.form-email .email-body .message-field textarea{
    width: 100%;
    height: 60px;
}

.form-email .email-body .message-field textarea:focus{
    outline: none;
}

.form-email .email-footer{
    background-color: #9f5b26;
    width: 100%;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0px 0px 10px 10px;
}

.form-email .email-footer .button-field .form-button{
    border: 0;
    padding: 10px 20px;
    font-family: gotham;
    border-radius: 5px;
    background-color: white;
    cursor: pointer;
}

@media (max-width: 768px) {
    .form-email{
        width: 80%;
    }   
    
    .form-email .email-header p{
        font-size: 30px;
    }
    
    .form-email .email-header a{
        font-size: 30px;
    }
    
    .form-email .email-body .text-field{
        margin-bottom: 10px;
    }
    
    .form-email .email-body .text-field p{
        margin-bottom: 10px;
    }
    
    .form-email .email-body .text-field input{
        height: 30px;
    }
    
    .form-email .email-body .message-field{
        margin-bottom: 10px;
    }
    
    .form-email .email-body .message-field p{
        margin-bottom: 10px;
    }
    
    .form-email .email-body .message-field textarea{
        height: 90px;
    }
    
    .form-email .email-footer .button-field .form-button{
        padding: 20px 30px;
        font-size: 20px;
    }
}

@media (min-width: 1920px) {
    .form-email{
        width: 500px;
    }   
    
    .form-email .email-header p{
        font-size: 30px;
    }
    
    .form-email .email-header a{
        font-size: 30px;
    }
    
    .form-email .email-body .text-field{
        margin-bottom: 10px;
    }
    
    .form-email .email-body .text-field p{
        margin-bottom: 10px;
    }
    
    .form-email .email-body .text-field input{
        height: 30px;
    }
    
    .form-email .email-body .message-field{
        margin-bottom: 10px;
    }
    
    .form-email .email-body .message-field p{
        margin-bottom: 10px;
    }
    
    .form-email .email-body .message-field textarea{
        height: 90px;
    }
    
    .form-email .email-footer .button-field .form-button{
        padding: 20px 30px;
        font-size: 20px;
    }
}